import { useDispatch, useSelector } from 'react-redux'
import { setLanguage, setPlatform, setTheme } from './reducer'
import { PlatFormType } from "./interface";
import { LanguageType, ThemeType } from 'static-resource';

// import { RootState } from 'stores'


export function useSettings() {
    const settings = useSelector((state: any) => state.settings)
    const themeMode = settings.themeMode
    const dispatch = useDispatch()
    const setThemeWrapper = (theme: keyof typeof ThemeType) => {
        dispatch(setTheme(theme))
    }
    const setLngWrapper = (theme: keyof typeof LanguageType) => {
        dispatch(setLanguage(theme))
    }
    const setPlatformWrapper = (theme: keyof typeof PlatFormType) => {
        dispatch(setPlatform(theme))
    }

    return {
        settings,
        themeMode,
        setThemeWrapper,
        setLngWrapper,
        setPlatformWrapper,
    }

}
