import { memo, useState } from 'react'
import styled from '@emotion/styled'
import { DropDownIcon } from 'static-resource'
import { Column, generateColumns, generateRows, Table } from '../../basic-lib'
import { SingleOrderHistoryTable } from './SingleOrderHistoryTable'
import { Popover, PopoverType } from '../../basic-lib'
import { WithTranslation } from 'react-i18next';

export enum OrderSide {
    sell = 'Sell',
    buy = 'Buy'
}

export interface AmountItem {
    type: string
    key: string
    value: number
}

export enum OrderStatus {
    filled = 'Filled',
    cancelled = 'Cancelled'
}

export interface OrderHistoryRow {
    side: OrderSide
    amount: AmountItem[]
    average: number
    filledAmount: AmountItem[]
    filledPrice: number
    time: number
    status: OrderStatus
    sortColumn: string
    filterColumn: string
    actionsStatus: object
}

export type OrderHistoryRawDataItem = (string | number | AmountItem[])[]

const LastDayPriceChangedCell: any = styled.span`
	color: ${(props: any) => {
    const {
        value,
        theme: {colorBase},
    } = props
    return value === 'Buy' ? colorBase.success : colorBase.error
}};
`

export interface OrderHistoryTableProps {
    rawData: OrderHistoryRawDataItem[]
}

const CellStatus = ({row, column, rowIdx}: any) => {
    const value = row[ column.key ]
    const popupId = `${column.key}-${rowIdx}`
    const [isOpen, setIsOpen] = useState(false)
    const RenderValue: any = styled.span`
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
		color: ${({theme}) => {
        const {colorBase} = theme
        return value === 'Filled'
            ? colorBase.textPrimary
            : colorBase.textSecondary
    }};
		width: 110px;
        padding-right: 10px;

		& svg {
			font-size: 14px;
			transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
			transform: ${() => isOpen ? 'rotate(180deg)' : ''};
		}
	`
    const popupContent = [
        [[{type: 'out', key: 'ETH', value: 2}, {type: 'in', key: 'LRC', value: 100}], 0.00334, 0.01223, 5, 100],
        [[{type: 'out', key: 'ETH', value: 2}, {type: 'in', key: 'LRC', value: 100}], 0.00334, 0.01223, 5, 100],
        [[{type: 'out', key: 'ETH', value: 2}, {type: 'in', key: 'LRC', value: 100}], 0.00334, 0.01223, 5, 100],
        [[{type: 'out', key: 'ETH', value: 2}, {type: 'in', key: 'LRC', value: 100}], 0.00334, 0.01223, 5, 100],
        [[{type: 'out', key: 'ETH', value: 2}, {type: 'in', key: 'LRC', value: 100}], 0.00334, 0.01223, 5, 100],
    ]

    const RenderPopover = styled.div`
		width: 700px;
		margin: 12px;
		max-height: 250px;

		.contentWrapper {
			max-height: 250px;
			overflow: scroll;
		}
	`
    const triggerContent =
        <div style={{width: 110}}>
            <RenderValue>
                {value}
                <DropDownIcon/>
            </RenderValue>
        </div>

    const popoverContent =
        <RenderPopover>
            <div className="arrowPopover"/>
            <div className="contentWrapper">
                <SingleOrderHistoryTable rawData={popupContent}/>
            </div>
        </RenderPopover>

    return <div className="rdg-cell-value">
        <Popover
            type={PopoverType.click}
            popupId={popupId}
            // children={triggerContent}
            popoverContent={popoverContent}
            handleStateChange={setIsOpen}
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'right',
            }}
            transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
            }}
            arrowHorizon={{
                right: 10
            }}
        >
            {triggerContent}
        </Popover>
    </div>
}

export const OrderHistoryTable = memo((props: OrderHistoryTableProps & WithTranslation) => {
    const actionColumns = ['status']
    const columnModeOrderHistory: Column<OrderHistoryRow, unknown>[] = [
        {
            key: 'side',
            name: 'Side',
            formatter: ({row, column}) => {
                const value = row[ column.key ]
                return (
                    <div className="rdg-cell-value">
                        <LastDayPriceChangedCell value={value}>
                            {value}
                        </LastDayPriceChangedCell>
                    </div>
                )
            },
        },
        {
            key: 'amount',
            name: 'Amount',
            formatter: ({row, column}) => {
                const [valueFirst, valueLast] = row[ column.key ]
                const {key: keyFirst, value: amountFirst} = valueFirst
                const {key: keyLast, value: amountLast} = valueLast
                const renderValue = `${amountFirst} ${keyFirst}->${amountLast} ${keyLast}`
                return <div className="rdg-cell-value">{renderValue}</div>
            },
        },
        {
            key: 'average',
            name: 'Average',
            formatter: ({row, column}) => {
                const value = row[ column.key ]
                const hasValue = Number.isFinite(value)
                const renderValue = hasValue ? value.toFixed(4) : '--'
                return <div className="rdg-cell-value">{renderValue}</div>
            },
        },
        {
            key: 'filledAmount',
            name: 'Filled Amount',
            formatter: ({row, column}) => {
                const [valueFirst, valueLast] = row[ column.key ]
                const {key: keyFirst, value: amountFirst} = valueFirst
                const {key: keyLast, value: amountLast} = valueLast
                const renderValue = `${amountFirst} ${keyFirst}->${amountLast} ${keyLast}`
                return <div className="rdg-cell-value">{renderValue}</div>
            },
        },
        {
            key: 'filledPrice',
            name: 'Filled Price',
            formatter: ({row, column}) => {
                const value = row[ column.key ]
                const unit = (row as any).amount[ 0 ].key;
                const hasValue = Number.isFinite(value)
                const renderValue = hasValue ? `${value.toFixed(4)} ${unit}` : '--'
                return (
                    <div className="rdg-cell-value">
                        <span>{renderValue}</span>
                    </div>
                )
            },
        },
        {
            key: 'time',
            name: 'Time',
            formatter: ({row, column}) => {
                const value = row[ column.key ]
                const renderValue = Number.isFinite(value)
                    ? `${value} Days`
                    : '--'
                return (
                    <div className="rdg-cell-value">
                        <span>{renderValue}</span>
                    </div>
                )
            },
        },
        {
            key: 'status',
            name: 'Status',
            formatter: ({row, column, rowIdx}) => <>
                <CellStatus {...{row, column, rowIdx}} />
            </>
        },
    ]

    const defaultArgs: any = {
        rawData: [],
        columnMode: columnModeOrderHistory,
        generateRows,
        generateColumns,
        actionColumns,
    }

    return <Table {...{...defaultArgs, ...props}} />

})
