import { HeaderRendererProps, SortableHeaderCell } from 'react-data-grid';
import { DropDownIcon } from 'static-resource';
import React from 'react';

export interface LoopringTBHeaderProps<R> extends HeaderRendererProps<R> {
    child?: React.ElementType<any> | JSX.Element,
}

export function LoopringTBHeader<R>({
                                        sortDirection,
                                        sortColumn,
                                        onSort,
                                        column,
                                        onAllRowsSelectionChange,
                                        child,
                                        ...props
                                    }: LoopringTBHeaderProps<R>) {



    function getCell() {
        // console.log(child)
        // const _child = () => {
        //     debugger
        //     if(React.isValidElement(child)) {
        //         return React.cloneElement<any>(child, { column:column });
        //     } else{
        //         return child
        //     }
        // }
        if (column.sortable) {
            return (
                <SortableHeaderCell
                    {...props}
                    column={column}
                    sortColumn={sortColumn}
                    onSort={onSort}
                    sortDirection={sortDirection}
                >
                    {child ? child : column.name}

                    <span className={`sort-icon ${sortColumn === column.key ? sortDirection : 'NONE'}`}><DropDownIcon/></span>
                </SortableHeaderCell>
            );
        }

        return child ? child : column.name;
    }

    return <>{getCell()}</>;
}
