import styled from '@emotion/styled'
import Button from '@material-ui/core/Button'
import { Column, generateColumns, generateRows, Table } from '../../basic-lib/tables/index'
import { StarIcon } from 'static-resource'
import { WithTranslation } from 'react-i18next'

interface Row {
    price: string
    size: string
    volume: string
    number: string
    sortColumn: string
    filterColumn: string
    cellExpend: {
        value: string
        children: []
        isExpanded: boolean
    }
    children?: Row[]
    isExpanded?: boolean
    format?: any
}

export type QuoteTableRawDataItem = (string | number | string[] | number[])[]

const CoinPairCell: any = styled.span`
	display: flex;
	align-items: center;
`

const QuoteTableChangedCell: any = styled.span`
	color: ${(props: any) => {
    const {theme: {colorBase}} = props
    return props.value > 0
        ? colorBase.success
        : props.value < 0
            ? colorBase.error
            : colorBase.textSecondary
}
}
`

const columnModelQuoteTable: Column<Row, unknown>[] = [
    {
        key: 'pair',
        name: 'Pair',
        sortable: true,
        formatter: ({row, column}) => {
            const [valueFirst, valueLast] = row[ column.key ]
            const RenderValue = styled.span`
				color: ${({theme}) => theme.colorBase.textSecondary}
			`
            return (
                <div className="rdg-cell-value">
                    <CoinPairCell>
                        <StarIcon/>
                        <span style={{marginLeft: 8, marginTop: 3}}>
							{valueFirst}
                            <RenderValue> / {valueLast}</RenderValue>
						</span>
                    </CoinPairCell>
                </div>
            )
        },
    },
    {
        key: 'lastPrice',
        name: 'Last Price',
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const [valueFirst, valueLast] = value
            const getRenderValue = (value: number) => Number.isFinite(value) ? value.toFixed(2) : '--'
            const RenderValue = styled.span`
				color: ${({theme}) => theme.colorBase.textSecondary}
			`
            return (
                <div className="rdg-cell-value">
                    <span>{getRenderValue(valueFirst)}<RenderValue> / {getRenderValue(valueLast)}</RenderValue></span>
                </div>
            )
        },
    },
    {
        key: 'change',
        name: '24h Change',
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const isPositive = value > 0
            const sign = isPositive ? '+' : ''
            const renderValue = hasValue ? `${sign}${value.toFixed(2)}%` : '--%'
            return (
                <div className="rdg-cell-value">
                    <QuoteTableChangedCell value={value}>
                        {renderValue}
                    </QuoteTableChangedCell>
                </div>
            )
        },
    },
    {
        key: 'high',
        name: '24h High',
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const renderValue = hasValue ? value.toFixed(2) : '--'
            return (
                <div className="rdg-cell-value">
                    <span>{renderValue}</span>
                </div>
            )
        },
    },
    {
        key: 'low',
        name: '24h Low',
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const renderValue = hasValue ? value.toFixed(2) : '--'
            return (
                <div className="rdg-cell-value">
                    <span>{renderValue}</span>
                </div>
            )
        },
    },
    {
        key: 'volume',
        name: '24h Volume',
        sortable: true,
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const renderValue = hasValue
                ? `${(value / 1000000).toFixed(2)} M`
                : '--'
            return (
                <div className="rdg-cell-value">
                    <span>{renderValue}</span>
                </div>
            )
        },
    },
    {
        key: 'trade',
        name: '',
        formatter: () => {
            return (
                <div className="rdg-cell-value">
                    <Button variant="outlined">Trade</Button>
                </div>
            )
        }
    }
]

const defaultArgs: any = {
    rawData: [],
    columnMode: columnModelQuoteTable,
    generateRows,
    generateColumns,
}

export interface QuoteTableProps {
    rawData: QuoteTableRawDataItem[]
}

export const QuoteTable = (props: QuoteTableProps & WithTranslation) => {
    return <Table {...{...defaultArgs, ...props}} />
}
