import { Box, Grid, Icon, Link, Typography } from '@material-ui/core/';
import { CopyIcon, LinkIcon, QRIcon } from '../../static-resource';
import { Button } from '../basic-lib';
import React from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { AccountInfoProps } from './Interface';
import styled from '@emotion/styled';
import { VipStyled } from './Styled';


const BoxStyled  = styled(Box)`
  .MuiLink-root{
    height: 2rem;
    line-height: 2rem;
    color: ${({theme}) => theme.colorBase.textSecondary};
    .MuiIcon-root{
      margin-top: -2px;
      margin-right: ${({theme}) => theme.unit/2}px;
    }
  }
` as typeof Box
const ButtonListStyled = styled(Grid)`    
    .MuiButton-root:not(:last-child){
        margin-right: ${({theme}) => theme.unit}px;
    }
`
export const AccountInfo = withTranslation('common')(({address,level,ethLink,mainBtn, onDisconnect, onSwith, onAdvanced, t}:AccountInfoProps & WithTranslation) => {
    const handleCopyOnClick = (event:React.MouseEvent)=>{
        console.log(event)
    }
    const  handleQROnClick  = (event:React.MouseEvent)=>{
        console.log(event)
    }
    return <Grid container  justifyContent={'space-between'} alignItems={'center'}>
        <Grid item xs={7} display={'flex'} flexDirection={'column'} >
            <Typography component={'p'} display={'flex'} alignItems={'center'} justifyContent={'flex-start'}>
                <Typography component={'span'} variant={'h4'}>{address}</Typography>
                {level ? <VipStyled component={'span'} variant={'body2'} alignSelf={'flex-start'}>{level}</VipStyled>:undefined}
            </Typography>
            <BoxStyled component={'p'} display={'flex'} alignItems={'center'} justifyContent={'flex-start'} marginTop={1}>
                <Typography component={'span'} variant={'body2'} paddingRight={3}>
                    <Link href={'#'} onClick={handleCopyOnClick}  display={'flex'} alignItems={'center'} justifyContent={'center'} >
                        <Icon><CopyIcon/></Icon>{t('labelCopyAddress')}</Link>
                    </Typography>
                <Typography component={'span'} variant={'body2'} paddingRight={3} >
                    <Link href={ethLink} onClick={handleCopyOnClick}  display={'flex'} alignItems={'center'} justifyContent={'center'} >
                        <Icon><LinkIcon/></Icon>{t('labelViewEth')}</Link>
                </Typography>
                <Typography component={'span'} variant={'body2'} paddingRight={3}>
                    <Link href={'http::eht'} onClick={handleQROnClick} display={'flex'} alignItems={'center'}justifyContent={'center'} >
                        <Icon><QRIcon/></Icon>{t('labelQRCode')}</Link>
                </Typography>
            </BoxStyled>
        </Grid>
        <ButtonListStyled item xs={5} display={'flex'} flexDirection={'row'} justifyContent={'flex-end'}>
                <Button onClick={() => { if (onSwith) onSwith() }} variant={'outlined'} size={'medium'} color={'primary'}>{t('labelSwitchAccount')}</Button>
                <Button onClick={() => { if (onDisconnect) onDisconnect() }} variant={'outlined'} size={'medium'} color={'primary'}>{t('labelDisconnect')}</Button>
                {mainBtn?mainBtn:<Button onClick={() => { if (onAdvanced) onAdvanced() }}  variant={'contained'} size={'small'} color={'primary'}>{t('labelGoAdvance')}</Button> }
        </ButtonListStyled>
    </Grid>
})
