import React from 'react'
import { Box, Grid, MenuItem, Menu } from '@material-ui/core'
import styled from '@emotion/styled'
import { WithTranslation, withTranslation } from 'react-i18next'
import PopupState, { bindTrigger, bindMenu } from 'material-ui-popup-state'
import { Button } from '../../basic-lib/btns'
import { Table, generateColumns, generateRows, Column } from '../../basic-lib/tables'
import { Filter } from './components/Filter'

interface Row {
    token: {
        type: TokenType;
        value: string;
    };
    amount: number;
    available: number;
    locked: number;
    filterColumn?: string
    tradePairList?: {
        first: string;
        last: string;
    }[]
    cellExpend?: {
        value: string
        children: []
        isExpanded: boolean
    }
    children?: Row[]
    isExpanded?: boolean
    format?: any
}

export enum TokenType {
    single = 'single',
    lp = 'lp'
}

export type TradePairItem = {
    first: string;
    last: string;
}

export type RawDataAssetsItem = {
    token: {
        type: TokenType,
        value: string
    }
    amount: number;
    available: number;
    locked: number;
    tradePairList?: TradePairItem[];
    smallBalance: boolean;
}

export interface AssetsTableProps {
    rawData: RawDataAssetsItem[]
}

export const AssetsTable = withTranslation()((props: WithTranslation & AssetsTableProps) => {
    const { t } = props
    const columnModeAssets: Column<Row, unknown>[] = [
        {
            key: 'token',
            name: t('Token'),
            formatter: ({row, column}) => {
                const token = row[column.key]
                const StyledToken = styled(Box)`
                    font-size: 13px;
                `
                return <StyledToken>{token.value}</StyledToken>
            }
        },
        {
            key: 'amount',
            name: t('Amount'),
        },
        {
            key: 'available',
            name: t('Available'),
        },
        {
            key: 'locked',
            name: t('Locked'),
        },
        {
            key: 'actions',
            name: t('Actions'),
            minWidth: 400,
            formatter: ({ row }) => {
                const token = row['token']
                const isLp = token.type === TokenType.lp
                const tradePairs: TradePairItem[] = row['_rawData'][4] || []
                
                return (
                    <Grid container spacing={1} alignItems={'center'}>
                        <Grid item>
                            <Button variant={'outlined'} size={'medium'} color={'primary'}>Receive</Button>
                        </Grid>
                        <Grid item>
                            <Button variant={'outlined'} size={'medium'} color={'primary'}>Send</Button>
                        </Grid>
                        <Grid item>
                            <Button variant={'outlined'} size={'medium'} color={'primary'}>Transfer</Button>
                        </Grid>
                        <Grid item>
                            {isLp
                                ? <Button variant={'outlined'} color={'primary'} disabled>AMM</Button>
                                : <PopupState variant="popover" popupId="demo-popup-menu">
                                    {(popupState) => (
                                        <>
                                            <Button variant={'outlined'} size={'medium'} color="primary" {...bindTrigger(popupState)}>
                                                Trade
                                            </Button>
                                            <Menu {...bindMenu(popupState)}>
                                                {tradePairs.map(({first, last}) => {
                                                    const value = `${first}/${last}`;
                                                    return  (
                                                        <MenuItem onClick={popupState.close} key={value} value={value}>{value}</MenuItem>
                                                    )
                                                })}
                                            </Menu>
                                        </>
                                    )}
                                </PopupState>
                            }
                        </Grid>
                    </Grid>
                )
            }
        },
        {
            key: 'tradePairList',
            name: 'tradePairList',
            hidden: true
        }
    ]
    const formattedRawData = props.rawData.map(o => Object.values(o))
    const [renderData, setRenderData] = React.useState(formattedRawData)
    const defaultArgs: any = {
        rawData: [],
        columnMode: columnModeAssets.filter(o => !o.hidden),
        generateRows,
        generateColumns,
        style: {
            backgroundColor: 'transparent'
        }
    }
    return <>
        <Filter originalData={formattedRawData} setData={setRenderData}  />
        <Table {...{...defaultArgs, ...props, rawData: renderData }}/>
    </>
})
