import { SwapData, SwapProps, SwapTradeData } from '../Interface';
import { withTranslation, WithTranslation } from 'react-i18next';
import React from 'react';
import { Grid } from '@material-ui/core';
import { SwitchPanel, SwitchPanelProps } from '../../basic-lib';
import { DropDownIcon, RroToLiteIcon, TradeCalcData } from 'static-resource';
import { useDeepCompareEffect } from 'react-use';
import clockLoading from '../../../assets/svg/clock-loading.svg';
import { IconButtonStyled, SwapMenuList, SwapTradeWrap } from '../components';


export const SwapPanel = withTranslation('common', {withRef: true})(<T extends SwapTradeData<I>,
    I,
    TCD extends TradeCalcData<I>>({
                                      tradeData,
                                      disabled,
                                      doBeforeSwitch,
                                      onCoinValueChange,
                                      tradeCalcData,
                                      handleError,
                                      onSwapClick,
                                      swapBtnStatus,
                                      tokenSellProps,
                                      tokenBuyProps,
                                      ...rest
                                  }: SwapProps<T, I, TCD> & WithTranslation) => {
    tradeData = (tradeData ? tradeData : {sell: {}, buy: {}}) as T;
    // const [tradeCalcData, setTradeCalcData] = React.useState<TCD>(rest.tradeCalcData);
    const [swapData, setSwapData] = React.useState<SwapData<T>>({
        type: 'buy',
        to: 'button',
        tradeData: {
            sell: tradeData.sell.belong ? {
                belong: tradeData.sell.belong,
                balance: tradeCalcData.walletMap[ tradeData.sell.belong ]?.count
            } : {},
            buy: tradeData.buy.belong ? {
                belong: tradeData.buy.belong,
                balance: tradeCalcData.walletMap[ tradeData.buy.belong ]?.count
            } : {},
        }
    } as SwapData<T>);
    const [index, setIndex] = React.useState(0);
    useDeepCompareEffect(() => {
        if (tradeData !== swapData.tradeData) {
            setSwapData({...swapData, tradeData: tradeData});
        }
    }, [tradeData])
    const onChangeIndex = React.useCallback(async (index: 0 | 1, {to, tradeData, type}: SwapData<T>) => {
        await doBeforeSwitch({to, tradeData, type}, type === 'exchange' ? 'exchange' : `${type}To${to}` as any);
        if (typeof rest.onChangeIndex == 'function') {
            setSwapData(rest.onChangeIndex(index, {to, tradeData, type}, tradeCalcData));
        } else {
            if (type === 'exchange') {
                const countBuy = tradeData.buy.belong ? tradeCalcData.walletMap[ tradeData.buy.belong ]?.count : 0;
                const countSell = tradeData.sell.belong ? tradeCalcData.walletMap[ tradeData.sell.belong ]?.count : 0;
                console.log('exchange')
                setSwapData({
                    tradeData: {
                        ...swapData.tradeData,
                        sell: {belong: tradeData.sell.belong, balance: countSell, tradeValue: 0},
                        buy: {belong: tradeData.buy.belong, balance: countBuy, tradeValue: 0},
                    } as T, to, type: 'buy'
                });
                // (oldValue)=>{
                //     oldValue.tradeData = {
                //         ...oldValue.tradeData,
                //         sell: {belong: tradeData.sell.belong, balance: countSell, tradeValue: 0},
                //         buy: {belong: tradeData.buy.belong, balance: countBuy, tradeValue: 0},
                //     };
                //     console.log(oldValue.tradeData.sell.belong,oldValue.tradeData.buy.belong)
                //     oldValue.to = to;
                //     oldValue.type = 'buy';
                //     return  oldValue
                // }
            } else if (to === 'menu') {
                //console.log(`${type} to menu`,tradeData.buy,tradeData.sell);
                setSwapData({tradeData, to, type});
            } else if (to === 'button') {
                const count = tradeData[ type ].belong ? tradeCalcData.walletMap[ tradeData[ type ].belong ]?.count : 0;
                let _tradeData = {
                    ...tradeData,
                    [ type ]: {belong: tradeData[ type ].belong, balance: count ? count : 0, tradeValue: 0}
                };
                // console.log(`${type} to button`,_tradeData.buy,_tradeData.sell);
                setSwapData({tradeData: _tradeData, to, type});
            }


        }
        setIndex(index);
    }, [doBeforeSwitch, tradeCalcData, rest]);
    const onSlippageChange = React.useCallback((slippage: number | string, customSlippage: number | string | undefined) => {
        setSwapData({
            ...swapData,
            tradeData: {
                ...swapData.tradeData,
                slippage,
                __cache__: {
                    ...swapData.tradeData.__cache__,
                    customSlippage: customSlippage
                }
            }
        });

        // (oldValue)=>{
        //     oldValue.tradeData =  {
        //         ...oldValue.tradeData,
        //         slippage,
        //         __cache__: {
        //             ...oldValue.tradeData.__cache__,
        //             customSlippage:customSlippage
        //         }
        //     } as T
        //     return  oldValue;
        // }
    }, [])
    const props: SwitchPanelProps<'tradeMenuList' | 'trade'> = {
        index: index, // show default show
        panelList: [
            {
                key: "trade",
                element: () => <SwapTradeWrap<T, I, TCD> key={"trade"} {...{
                    ...rest,
                    onChangeIndex,
                    tradeCalcData,
                    onSwapClick,
                    onSlippageChange,
                    onCoinValueChange,
                    disabled,
                    swapBtnStatus,
                    swapData,
                    tokenSellProps,
                    tokenBuyProps,
                    handleError,
                }}/>,
                toolBarItem: () => <Grid container justifyContent={'flex-end'}>
                    <IconButtonStyled edge="end"
                                      className={'switch outline'}
                                      color="inherit"
                                      aria-label="to Professional">
                        <RroToLiteIcon/>
                    </IconButtonStyled>
                    <IconButtonStyled edge="end"
                                      className={'clock-loading outline'}
                                      color="inherit"
                                      aria-label="3' price update">
                        <img src={clockLoading} alt={'loading'} width={28} height={28}/>
                    </IconButtonStyled>
                </Grid>
            },
            {
                key: "tradeMenuList",
                element: () => <SwapMenuList<T, I, TCD> key={"tradeMenuList"} {...{
                    ...rest,
                    onChangeIndex,
                    tradeCalcData,
                    swapData
                }}/>,
                toolBarItem: () => <Grid container justifyContent={'flex-start'}>
                    <IconButtonStyled edge="start" sx={{transform: 'rotate(90deg)', transformOrigin: '50%'}}
                                      className={'switch'}
                                      color="inherit"
                                      onClick={() => onChangeIndex(0, swapData)}
                                      aria-label="to Professional">

                        <DropDownIcon/>
                    </IconButtonStyled>

                </Grid>
            },
        ],

    }
    return <SwitchPanel {...{...rest, ...props}} />
})

export const TransferMode = withTranslation()

