import { Column, generateColumns, generateRows, Table, } from '../../basic-lib'
import { AmountItem, OrderHistoryRawDataItem } from './OrderHistoryTable'
import { WithTranslation } from 'react-i18next';

interface Row {
    amount: AmountItem[]
    tradingPrice: number
    filledPrice: number
    time: number
    total: number
    sortColumn: string
    filterColumn: string
    actionsStatus: object
}

const columnModeSingleHistory: Column<Row, unknown>[] = [
    {
        key: 'amount',
        name: 'Amount',
        formatter: ({row, column}) => {
            const [valueFirst, valueLast] = row[ column.key ]
            const {key: keyFirst, value: amountFirst} = valueFirst
            const {key: keyLast, value: amountLast} = valueLast
            const renderValue = `${amountFirst} ${keyFirst}->${amountLast} ${keyLast}`
            return <div className="rdg-cell-value">{renderValue}</div>
        },
    },
    {
        key: 'tradingPrice',
        name: 'Trading Price',
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const renderValue = hasValue ? value.toFixed(5) : '--'
            return <div className="rdg-cell-value">{renderValue}</div>
        },
    },
    {
        key: 'filledPrice',
        name: 'Filled Price',
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const hasValue = Number.isFinite(value)
            const renderValue = hasValue ? value.toFixed(5) : '--'
            return <div className="rdg-cell-value">{renderValue}</div>
        },
    },
    {
        key: 'time',
        name: 'Time',
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const renderValue = Number.isFinite(value)
                ? `${value} Days`
                : '--'
            return (
                <div className="rdg-cell-value">
                    <span>{renderValue}</span>
                </div>
            )
        },
    },
    {
        key: 'total',
        name: 'Total',
        formatter: ({row, column}) => {
            const value = row[ column.key ]
            const unit = (row as any).amount[ 1 ].key;
            const hasValue = Number.isFinite(value)
            const renderValue = hasValue ? `${value.toFixed(4)} ${unit}` : '--'
            return (
                <div className="rdg-cell-value">
                    <span>{renderValue}</span>
                </div>
            )
        },
    },
]

const defaultArgs: any = {
    rawData: [],
    columnMode: columnModeSingleHistory,
    generateRows,
    generateColumns,
    // actionColumns,
}

export interface SingleOrderHistoryTableProps {
    rawData: OrderHistoryRawDataItem[]
}

export const SingleOrderHistoryTable = (props: SingleOrderHistoryTableProps & WithTranslation) => {
    return <Table {...{...defaultArgs, ...props}} />
}
